@import "./../utils/mixins";

.category {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 130px);
  gap: 0 20px;
  grid-column: 1 / 3;

  @include for-tablet() {
    grid-template-columns: repeat(6, 80px);
    gap: 0 24px;
  }

  @include for-desktop() {
    grid-template-columns: repeat(12, 63px);
    gap: 0 24px;
  }

  &__article {
    grid-column: 1 / 3;

    @include for-tablet() {
      grid-column: 1 / 7;
    }

    @include for-desktop() {
      grid-column: 1 / 13;
    }
  }
}
